<template>
  <div class="home">
    <div
      v-if="loading"
      class="spinner-border text-light text-center"
      role="status"
    >
      <span class="sr-only">Loading...</span>
    </div>
    <div class="pt-3 pr-2 float-right" id="input">
      <input
        @search="getData"
        v-model="query"
        class="input text-light pl-3 mr-2"
        type="search"
        name="search"
        placeholder="Locations"
      /><i @click="getData" class="text-light fa fa-search"></i>
    </div>

    <div class="pt-5 text-light text-center font-weight-bold">
      <p class="pt-3"><i class="pr-2 fa fa-map-marker"></i>{{ query }}</p>
      <p class="text-secondary">{{ date }}</p>
      <p class=" temp"><img :src="src" class="" /> {{ temp }}&#8451;</p>
      <p>{{ min }}&#8451; / {{ max }}&#8451; Feels like {{ feel }}&#8451;</p>
      <p class="">{{ main }}</p>
    </div>

    <div class="box d-md-flex d-lg-flex">
      <div class="box-1 text-light font-weight-bold d-flex mt-lg-4">
        <span class=" pl-3 pl-lg-5 pt-4 pt-lg-5">
          Wind Speed <br />
          {{ speed }}m/s
        </span>
        <span class="span mx-5"></span>
        <span class="pr-3 pt-4 pt-lg-5">
          Wind Degree <br />
          {{ deg }}&#8451;
        </span>
      </div>

      <div class="box-2  text-light font-weight-bold d-flex">
        <span class="text-center pl-2 pt-2 pt-lg-1">
          Min_Temp <br />
          <p class=" temp"><img :src="src" class="img" /></p>
          <small>{{ min }}&#8451;</small>
        </span>

        <span class="text-center pl-2 pt-2 pt-lg-1">
          Max_Temp
          <br />
          <p class=" temp"><img :src="src" class="img" /></p>
          <small>{{ max }}&#8451;</small>
        </span>

        <span class="text-center pl-2 pt-2 pt-lg-1">
          Pressure <br />
          <p class=" temp"><img :src="src" class="img" /></p>
          <small>{{ pressure }}hPa</small>
        </span>

        <span class="text-center pl-2 pt-2 pt-lg-1">
          Humidity <br />
          <p class=" temp"><img :src="src" class="img" /></p>
          <small>{{ humidity }}%</small>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "home",
  data() {
    return {
      loading: false,
      date: "",
      query: "London",
      apiKey: "7e57d6aa939d39412d03ec705e5e5bbd",
      val: {},
      main: "",
      src: "",
      temp: "",
      min: "",
      max: "",
      feel: "",
      speed: "",
      deg: "",
      pressure: "",
      humidity: "",
    };
  },
  methods: {
    loadData() {
      this.main = this.val.weather[0].main;
      this.src = `http://openweathermap.org/img/wn/${this.val.weather[0].icon}@2x.png`;
      this.temp = this.val.main.temp;
      this.min = this.val.main.temp_min;
      this.max = this.val.main.temp_max;
      this.feel = this.val.main.feels_like;
      this.speed = this.val.wind.speed;
      this.deg = this.val.wind.deg;
      this.date = new Date();
      this.date = this.date.toDateString();
      this.pressure = this.val.main.pressure;
      this.humidity = this.val.main.humidity;
    },
    getData() {
      const newquery1 = this.query.slice(0, 1)[0].toUpperCase();
      const newquery2 = this.query.slice(1, this.query.length);
      this.query = `${newquery1}${newquery2}`;
      axios
        .get(
          `https://api.openweathermap.org/data/2.5/weather?q=${this.query}&units=metric&appid=${this.apiKey}`
        )
        .then((res) => {
          this.val = res.data;
          this.loadData();
        })
        .catch((err) => console.log(err));
    },
  },
  created() {
    this.getData();
  },
};
</script>

